<template>
  <main class="mt-0 main-content">
    <section>
      <div class="page-header min-vh-100">
        <div class="container">
          <div class="row">
            <div class="mx-auto col-xl-4 col-lg-5 col-md-7 d-flex flex-column mx-lg-0">
              <alert-component v-if="loginFailed" :color="'danger'">Failed to login</alert-component>
              <div class="card card-plain">
                <div class="pb-0 card-header text-start">
                  <h4 class="font-weight-bolder">Sign In</h4>
                  <p class="mb-0">Enter your email and password to sign in</p>
                </div>
                <div class="card-body">
                  <form @submit.prevent="login()">
                    <div class="mb-3">
                      <input-component
                        @input="emailOrPhoneUpdate"
                        type="text"
                        placeholder="email or Phone number"
                        name="emailOrPhone"
                        size="lg"
                      />
                    </div>
                    <div class="mb-3">
                      <input-component
                        type="password"
                        placeholder="Password"
                        name="password"
                        size="lg"
                        @input="passwordUpdate"
                      />
                    </div>

                    <div class="text-center">
                      <button-component
                        class="mt-4"
                        variant="gradient"
                        color="success"
                        fullWidth
                        size="lg"
                        :isLoading="loadingStatus"
                      >
                        Sign in
                      </button-component>
                    </div>
                  </form>
                </div>
              </div>
            </div>
            <div
              class="top-0 my-auto text-center col-6 d-lg-flex d-none h-100 pe-0 position-absolute end-0 justify-content-center flex-column"
            >
              <div
                class="position-relative bg-gradient-primary h-100 m-3 px-7 border-radius-lg d-flex flex-column justify-content-center overflow-hidden"
                style="
                  background-image: url('https://raw.githubusercontent.com/creativetimofficial/public-assets/master/argon-dashboard-pro/assets/img/signin-ill.jpg');
                  background-size: cover;
                "
              >
                <span class="mask bg-gradient-success opacity-6"></span>
                <h4 class="mt-5 text-white font-weight-bolder position-relative">"Attention is the new currency"</h4>
                <p class="text-white position-relative">
                  The more effortless the writing looks, the more effort the writer actually put into the process.
                </p>
              </div>
            </div>
          </div>
        </div>
      </div>
    </section>
  </main>
</template>

<script>
  import ButtonComponent from '@/components/Button.vue';
  import InputComponent from '@/components/Input.vue';
  import AlertComponent from '@/components/Alert.vue';
  import { mapState } from 'vuex';
  const body = document.getElementsByTagName('body')[0];

  export default {
    name: 'signin-component',
    components: {
      ButtonComponent,
      InputComponent,
      AlertComponent,
    },
    data() {
      return {
        form: { emailOrPhone: '', password: '' },
      };
    },
    created() {
      this.$store.state.hideConfigButton = true;
      this.$store.state.showNavbar = false;
      this.$store.state.showSidenav = false;
      this.$store.state.showFooter = false;
      body.classList.remove('bg-gray-100');
    },
    beforeUnmount() {
      this.$store.state.hideConfigButton = true;
      this.$store.state.showNavbar = false;
      this.$store.state.showSidenav = true;
      this.$store.state.showFooter = true;
      body.classList.add('bg-gray-100');
    },
    computed: {
      ...mapState(['AuthenticationStore']),
      loadingStatus() {
        return this.$store.state.AuthenticationStore.loading;
      },
      loginFailed() {
        return this.$store.state.AuthenticationStore.loginFailed;
      },
    },
    methods: {
      login() {
        this.$store.dispatch('AuthenticationStore/login', this.form);
      },
      emailOrPhoneUpdate(event) {
        this.form.emailOrPhone = event.target.value;
      },
      passwordUpdate(event) {
        this.form.password = event.target.value;
      },
    },
  };
</script>
